<template>
	<view class="container" v-if="isshow">
		<watermark></watermark>
		<overdueList :personList="personList" :pageIndex="index"></overdueList>
	</view>
</template>

<script>
	import overdueList from '@/pages/salemanVisual/needDeal/components/overdueList.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import watermark from '@/components/watermark.vue';
	export default {
		data() {
			return {
				index:'', // 索引
				isshow:false,
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				personList:[], // 列表
			}
		},
		onLoad(options){
			this.index = options ? options.index : this.index
			switch (this.index) {
				case '0':
					uni.setNavigationBarTitle({
						title: '逾期未认购催办'
					})
					break
				case '1':
					uni.setNavigationBarTitle({
						title: '逾期未签约催办'
					})
					break
			}
			this._getList()
		},
		methods:{
			// 获取列表
			_getList() {
				let that = this
				let postCode = that.index==0?'2030':'2031'
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest(postCode, Object.assign({}, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.personList = that.etype == 3 ? that.personList.concat(res.data.data) : res.data.data
						that.totalPages = res.data.totalPages
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this._getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this._getList()
				}
			},
		},
		components: {
			overdueList, watermark
		}
	}
</script>

<style lang="scss">
	page{
		height: 100%;
		background-color: #f6f6f6;
	}
</style>
